import React, { useState } from 'react'
import ReactLoading from 'react-loading';



export default function CreateProduct(props) {
    
    const [productName, setProductName] = useState(null)
    const [productDes, setProductDes] = useState(null)
    const [productQuantity, setProductQuantity] = useState(null)
    const [error, setError] = useState(null)
    const [btn, setBtn] = useState(false)

    const createProduct = (e) => {
        e.preventDefault()
    }

    return (
        <div>
            <div className="col-md-5 offset-md-3">
            <h2>Add Product</h2>
            <form onSubmit={createProduct}>
                {error && <h6 className="text-danger text-center">{error}!</h6>}
                <div className="form-group">
                    <label for="exampleInputEmail1">Product Name</label>
                    <input
                        type="name"
                        className="form-control form-control-sm"
                        onChange={e => setProductName(e.target.value)}
                        required />
                </div>
                
                <div className="form-group">
                    <label for="exampleInputEmail1">Description</label>
                    <input
                        type="text"
                        className="form-control form-control-sm"
                        onChange={e => setProductDes(e.target.value)}
                        required />
                </div>

                <div className="form-group">
                    <label for="exampleInputEmail1">Total Quantity</label>
                    <input
                        type="number"
                        className="form-control form-control-sm"
                        onChange={e => setProductQuantity(e.target.value)}
                        required />
                </div>

                {btn ? <ReactLoading type="balls" color="#0d6efd" /> :
                    <button type="submit" className="btn btn-primary float-right mt-2">Add Product</button>}

            </form>

        </div>
        </div>
    )
}
