import React, { useState } from 'react'
import ReactLoading from 'react-loading';

export default function CreateSeller(props) {
    const [name, setname] = useState(null)
    const [contact, setcontact] = useState(null)
    const [email, setEmail] = useState(null)
    const [password, setPassword] = useState(null)
    const [error, setError] = useState(null)
    const [btn, setBtn] = useState(false)

    const createSeller = (e) =>{
        e.preventDefault()
    }


    return (
        <div className="col-md-5">
            <h2>Create Seller</h2>
            <form onSubmit={createSeller}>
                {error && <h6 className="text-danger text-center">{error}!</h6>}
                <div className="form-group">
                    Name:
                    <input
                        type="name"
                        className="form-control form-control-sm"
                        onChange={e => setname(e.target.value)}
                        required />
                </div>
                
                <div className="form-group">
                    Email address:
                    <input
                        type="email"
                        className="form-control form-control-sm"
                        onChange={e => setEmail(e.target.value)}
                        required />
                </div>

                <div className="form-group">
                Contact:
                    <input
                        type="text"
                        className="form-control form-control-sm"
                        onChange={e => setcontact(e.target.value)}
                        required />
                </div>


                <div className="form-group">
                    Password:
                    <input
                        type="password"
                        name="password"
                        className="form-control form-control-sm"
                        onChange={e => setPassword(e.target.value)}
                        required />
                </div>


                {btn ? <ReactLoading type="balls" color="#0d6efd" /> :
                    <button type="submit" className="btn btn-primary float-right mt-2">Create Seller</button>}

            </form>

        </div>
    )
}
