import React from "react";
import { Router, Location } from "@reach/router";
import { CSSTransition } from "react-transition-group";
import Login from "./components/login"
import CreateSeller from "./components/create_seller"
import PageNotFound from "./components/page_not_found";
import SellerList from "./components/seller_list";
import SellerProductList from "./components/seller_product_list";

import CreateProduct from "./components/create_product";
import ProductList from "./components/product_list";

const FadeTransitionRouter = (props) => (
    <Location>
        {({ location }) => (
            <div>
                <CSSTransition key={location.key} classNames="fade" timeout={500}>
                    <Router location={location}>{props.children}</Router>
                </CSSTransition>
            </div>
        )}
    </Location>
);




const AppRouter = () => (
    <FadeTransitionRouter>
        <PageNotFound default />
        <Login path="/" />
        <CreateSeller path="/create-seller" />
        <SellerList path="/seller-list" />
        <SellerProductList path="/seller-product-list/:seller_id" />

        <CreateProduct path="/create-product" />
        <ProductList path="/products" />
    </FadeTransitionRouter>

);

export default AppRouter;